<template>
	<div class="_page zipPic_page">
		<Header>
			<h1 slot="title"></h1>
		</Header>

		<div class="section flex-xc-yc">
			<div
				class="file-upload flex-xc-yc"
				v-show="fileList.length == 0"
			>
				<el-upload
					action=""
					:on-change="onChange"
					:http-request="httpRequest"
					multiple
					drag
					:limit="100"
					:file-list="fileList"
					accept=".png,.jpg,.jpeg,.gif,.raw,.tiff,.bmp"
				>
					<i class="el-icon-circle-plus"></i>
					<div class="el-upload__text"><em></em></div>
					<div
						slot="tip"
						class="el-upload__tip"
					>
						jpg/png,30
					</div>
				</el-upload>
			</div>
			<div
				class="sel-list"
				v-show="fileList.length != 0"
			>
				<div class="top flex-xb">
					<div class="config">
						<el-form
							ref="form"
							:model="config"
							label-width="3em"
							class="flex"
							:disabled="disabledForm"
						>
							<el-form-item>
								<span style="margin-right: 10px">
									<el-button
										type="text"
										:disabled="config.mode"
									>
										
									</el-button>
								</span>
								<el-switch v-model="config.mode"> </el-switch>
								<span style="margin-left: 10px">
									<el-button
										type="text"
										:disabled="!config.mode"
									>
										
									</el-button>
								</span>
							</el-form-item>
							<el-form-item v-if="!config.mode">
								<!-- <el-input v-model="config.ratio">
									<template slot="append"> 0 ~ 1</template>
								</el-input> -->

								<div class="flex">
									<el-slider
										style="width: 200px"
										v-model="config.ratio"
										:min="0.01"
										:max="0.99"
										:step="0.01"
									></el-slider>
									<span style="margin-left: 20px">
										<el-button
											type="text"
											disabled
										>
											{{ config.ratio }}
										</el-button>
									</span>
								</div>
							</el-form-item>
							&nbsp;&nbsp;&nbsp;
							<el-form-item v-if="config.mode">
								<el-input v-model.trim="config.compressTarget">
									<template slot="append"> kb</template>
								</el-input>
							</el-form-item>
						</el-form>
					</div>
					<el-button
						type="primary"
						style="margin-bottom: 22px"
						@click="startZip"
						:disabled="disabledForm"
					>
						{{ btnStatus }}
					</el-button>
				</div>
				<div class="sel-list-ul">
					<div class="sel-list-li th flex">
						<div class="name"></div>
						<div class="size"></div>
						<div class="size"></div>
						<div class="size"></div>
						<div class="size"></div>
					</div>
					<div
						class="sel-list-li flex-yc"
						v-for="(item, index) in fileList"
						:key="index"
					>
						<div class="name">{{ item.name }}</div>
						<div class="size">{{ item.size | parseSize }}</div>
						<div
							class="size"
							v-if="zipFileList[index]"
						>
							{{ zipFileList[index].file.size | parseSize }}
						</div>
						<div
							class="size"
							v-if="zipFileList[index]"
							style="color: #67c23a"
						>
							-{{ 100 - parseInt((zipFileList[index].file.size / item.size) * 100) }}%
						</div>
						<div
							class="size"
							v-if="zipFileList[index]"
						>
							<el-button
								size="mini"
								icon="el-icon-download"
								@click="downEv(zipFileList[index])"
							>
							</el-button>
						</div>
					</div>
				</div>
				<div
					class="flex-xb"
					style="margin-top: 22px"
				>
					<el-link disabled>{{ this.fileList.length }}</el-link>
					<div class="flex">
						<el-button
							type="danger"
							@click="clearAll"
						>
							
						</el-button>
						<el-button
							type="info"
							@click="downAll"
							v-show="zipFileList.length == fileList.length"
						>
							{{ this.zipFileList.length }}
						</el-button>
					</div>
				</div>
			</div>
		</div>
		<div
			class="fixedLoading flex-xc-yc flex-column"
			v-if="loading"
		>
			<p>
				<i class="el-icon-loading"></i>
				{{ ' ,  ' + fileList.length + ' ' }}
			</p>
			<br />
			<br />
			<div style="background: #fff; padding: 10px; border-radius: 20px">
				<el-progress
					type="circle"
					:percentage="percentage"
				></el-progress>
			</div>
		</div>
	</div>
</template>

<script>
import Header from '@/components/Header.vue';
import * as imageConversion from 'image-conversion';
import JSZip from 'jszip';
import FileSaver from 'file-saver';
export default {
	name: 'zipPic',
	components: { Header },
	data() {
		return {
			loading: false,
			hidenConfig: false,
			disabledForm: false,
			config: {
				mode: false,
				ratio: 0.6,
				compressTarget: null,
			},
			fileList: [],
			zipFileList: [],
			percentage: 0,
			btnStatus: '',
		};
	},
	methods: {
		httpRequest() {},
		onChange(file, fileList) {
			// console.log(file, fileList);
			this.fileList = fileList;
		},
		async startZip() {
			console.time('');
			this.disabledForm = true;
			this.loading = true;
			let fileObjList = [];

			//1s
			let length = this.fileList.length;
			let timer = 0;
			let timeClear = null;
			timeClear = setInterval(() => {
				if (this.percentage < 99) {
					timer = timer + 1;
					this.percentage = parseInt((timer / length) * 100);
				} else {
					clearInterval(timeClear);
				}
			}, 1000);
			for (var i = 0; i < this.fileList.length; i++) {
				let x = this.fileList[i];
				// 
				let comRes = await this.compressImg(x);
				fileObjList.push({
					name: x.name,
					type: x.raw.type,
					file: comRes,
				});
				// console.log(fileObjList, comRes);
			}
			this.zipFileList = fileObjList;
			this.percentage = 100;
			this.disabledForm = false;
			this.btnStatus = '';
			this.loading = false;
			console.timeEnd('');
		},

		compressImg(file) {
			// console.log(file);
			const size = file.size / 1024;
			let limit = 0;
			//
			// limit = (file.raw.size * this.config.ratio) / 1024;
			// if (this.config.compressTarget && limit > this.config.compressTarget) {
			// 	limit = this.config.compressTarget;
			// }
			if (this.config.mode) {
				limit = this.config.compressTarget;
			} else {
				limit = (file.raw.size * this.config.ratio) / 1024;
			}
			return new Promise((resolve) => {
				if (file.raw.size > limit) {
					imageConversion.compressAccurately(file.raw, limit).then((res) => {
						// console.log(`${res.size / 1024}k`);
						resolve(res);
					});
				} else {
					resolve(file);
				}
			});
		},
		downEv(file) {
			FileSaver.saveAs(file.file, file.name); // file-saver 
		},
		downAll() {
			this.filesToRar(this.zipFileList, 'tool-');
		},
		clearAll() {
			this.fileList = [];
			this.zipFileList = [];
			this.disabledForm = false;
		},

		/**
		 * arrImages:list:[{fileUrl:url,renameFileName:}]
		 * filename 
		 * */
		filesToRar(arrImages, filename) {
			let _this = this;
			let zip = new JSZip();
			let cache = {};
			this.$message.info('');
			for (let item of arrImages) {
				// const promise = _this.getImgArrayBuffer(item.fileUrl).then((data) => {
				// 	// , ArrayBuffer(blob)
				// });

				zip.file(item.name, item.file, { binary: true }); // 
				cache[item.name] = item.file;
			}
			zip.generateAsync({ type: 'blob' }).then((content) => {
				// 
				FileSaver.saveAs(content, filename); // file-saver 
				_this.$message.success('');
			});
		},
	},
	filters: {
		parseSize(size) {
			var sizeKb = size / 1024;
			var res = null;
			if (sizeKb > 1023) {
				res = sizeKb / 1024; //M
				res = parseInt(res * 100) / 100;
				res = res + 'M';
			} else {
				res = sizeKb;
				res = parseInt(res * 100) / 100;
				res = res + 'k';
			}
			return res;
		},
	},
};
</script>
<style scoped>
.zipPic_page {
	width: 100%;
	height: 100%;
	padding-top: 48px;
	background: linear-gradient(225deg, #c7e1ff, #386fb4);
	padding: 180px 0;
}
.section {
	position: relative;
}
.file-upload {
	width: 1000px;
	padding: 100px 0;
	border: 1px dashed #a8c1e2;
	background: #fff;
}
.file-upload .el-icon-circle-plus {
	font-size: 67px;
	color: #c0c4cc;
	margin: 40px 0 16px;
	line-height: 50px;
}
.sel-list {
	width: 1000px;
	padding: 50px 20px;
	border: 1px dashed #a8c1e2;
	background: #fff;
}
.sel-list-li {
	padding: 15px 30px;
	border: solid rgba(0, 0, 0, 0.03);
	border-width: 0 0 1px;
	font-size: 14px;
	color: #383838;
}
.sel-list-li.th {
	color: #afaeae;
}
.sel-list-li .name {
	width: 200px;
	margin-right: 2em;

	word-break: break-all;
	display: -webkit-box;
	display: box;
	overflow: hidden;
	text-overflow: ellipsis;
	-webkit-box-orient: vertical;
	-webkit-line-clamp: 1;
}
.sel-list-li .size {
	width: 100px;
	margin-right: 2em;
	word-break: break-all;
	display: -webkit-box;
	display: box;
	overflow: hidden;
	text-overflow: ellipsis;
	-webkit-box-orient: vertical;
	-webkit-line-clamp: 1;
}

.fixedLoading {
	position: fixed;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	background: rgba(0, 0, 0, 0.8);
	color: #fff;
	font-size: 14px;
}
.fixedLoading i {
	margin-right: 1em;
}
</style>
